import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { ScullyRoutesService } from '@scullyio/ng-lib';
import { map } from 'rxjs/operators';
import { AppStateService } from '../app.state.service';
import { BasePageComponent } from '../base-page.component';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent extends BasePageComponent {

  widthImage = 0;

  @ViewChild('container')
  imgContain: ElementRef<HTMLElement>;

  routes$ = this.scullyRoutesService.available$.pipe(
    map((routes) => routes.filter((route) => route.route.includes('/home'))),
  );
  constructor(private readonly scullyRoutesService: ScullyRoutesService, router: Router, appState: AppStateService) {
    super(router, appState);
  }
}